<template>
	<div id="advertise-index">
		<public-header title="店铺活动"></public-header>
		<div class="content">
			<div class="wrap-search">
				<div class="tab-group">
					<div class="tab-item active">新建推广</div>
					<router-link tag="div" class="tab-item" :to="util.getUrl({path: 'pages/advertise/list'})">已推广的活动</router-link>
				</div>
			</div>
			<van-list class="list" v-if="advertise && advertise.type">
				<div class="list-item">
					<div class="flex-lr margin-15-b">
						<div class="title">为您优选</div>
						<router-link tag="div" class="create" :to="util.getUrl({path: 'pages/advertise/recommend'})" v-if="advertise.type.recommend && advertise.type.recommend.status == 1">立即推广</router-link>
						<div class="create" v-else>广告未开售,敬请关注</div>
					</div>
					<div class="flex-lr font-12">
						<div>购买商户为您优选推广,吸引更多用户下单</div>
					</div>
				</div>
				<div class="list-item">
					<div class="flex-lr margin-15-b">
						<div class="title">商户置顶</div>
						<router-link tag="div" class="create" :to="util.getUrl({path: 'pages/advertise/stick', query: {type: 'stick'}})" v-if="advertise.type.stick && advertise.type.stick.status == 1">立即推广</router-link>
						<div class="create" v-else>广告未开售,敬请关注</div>
					</div>
					<div class="flex-lr font-12">
						<div>购买商户置顶推广,吸引更多用户下单</div>
					</div>
				</div>
				<div class="list-item">
					<div class="flex-lr margin-15-b">
						<div class="title">首页顶部</div>
						<router-link tag="div" class="create" :to="util.getUrl({path: 'pages/advertise/stick', query: {type: 'slideHomeTop'}})" v-if="advertise.type.slideHomeTop && advertise.type.slideHomeTop.status == 1">立即推广</router-link>
						<div class="create" v-else>广告未开售,敬请关注</div>
					</div>
					<div class="flex-lr font-12">
						<div>购买首页顶部幻灯片推广,吸引更多用户下单</div>
					</div>
				</div>
				<div class="list-item">
					<div class="flex-lr margin-15-b">
						<div class="title">会员中心</div>
						<router-link tag="div" class="create" :to="util.getUrl({path: 'pages/advertise/stick', query: {type: 'slideMember'}})" v-if="advertise.type.slideMember && advertise.type.slideMember.status == 1">立即推广</router-link>
						<div class="create" v-else>广告未开售,敬请关注</div>
					</div>
					<div class="flex-lr font-12">
						<div>购买会员中心幻灯片推广,吸引更多用户下单</div>
					</div>
				</div>
				<div class="list-item">
					<div class="flex-lr margin-15-b">
						<div class="title">顾客支付页</div>
						<router-link tag="div" class="create" :to="util.getUrl({path: 'pages/advertise/stick', query: {type: 'slidePaycenter'}})" v-if="advertise.type.slidePaycenter && advertise.type.slidePaycenter.status == 1">立即推广</router-link>
						<div class="create" v-else>广告未开售,敬请关注</div>
					</div>
					<div class="flex-lr font-12">
						<div>购支付页幻灯片推广,吸引更多用户下单</div>
					</div>
				</div>
				<div class="list-item">
					<div class="flex-lr margin-15-b">
						<div class="title">订单详情页</div>
						<router-link tag="div" class="create" :to="util.getUrl({path: 'pages/advertise/stick', query: {type: 'slideOrderDetail'}})" v-if="advertise.type.slideOrderDetail && advertise.type.slideOrderDetail.status == 1">立即推广</router-link>
						<div class="create" v-else>广告未开售,敬请关注</div>
					</div>
					<div class="flex-lr font-12">
						<div>购买订单详情页幻灯片推广,吸引更多用户下单</div>
					</div>
				</div>
			</van-list>
		</div>
		<iloading v-if="showPreLoading"></iloading>
	</div>
</template>

<script>
	import publicHeader from "@/components/header"
	import {getData} from "@/controller/funcCommon"
	export default {
		components: {
			publicHeader,
		},
		data() {
			return {
				showPreLoading: true,
				advertise: {},
			}
		},
		methods: {
			onLoad() {
				var that = this;
				getData({
					vue: that,
					url: 'manage/advertise/index',
					success: function(res) {
						that.advertise = res.advertise;
					}
				})
			},
		},
		mounted() {
			this.onLoad();
		}
	}
</script>

<style>
	#advertise-index .list-item{
		margin: 15px;
		padding: 15px;
		border-radius: 5px;
		background: url("../../../static/img/activity_bg_1.png") no-repeat;
		background-size: 100% 100%;
		color: #fff;
	}
	#advertise-index .list-item .create{
		background-color: #fff;
		color: #FD6B38;
		font-size: 12px;
		padding: 5px 10px;
		border-radius: 20px;
	}
</style>


